.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 56px;
  border-bottom: 1px solid var(--border-color);
  background: var(--bg-primary);
  font-family: var(--font-mono);
  font-size: 0.9rem;
  position: sticky;
  top: 0;
  z-index: 1000;
}

.logo {
  padding: 0 1.5rem;
  border-right: 1px solid var(--border-color);
  height: 100%;
  display: flex;
  align-items: center;
  width: 300px;
  color: var(--text-secondary);
}

.name {
  color: var(--text-secondary);
  font-weight: 500;
}

.mobileToggle {
  display: none;
  color: var(--text-secondary);
  padding: 0 1rem;
  z-index: 1001;

  &:hover {
    color: var(--text-primary);
  }
}

.links {
  display: flex;
  height: 100%;
}

.link {
  display: flex;
  align-items: center;
  padding: 0 2rem;
  border-right: 1px solid var(--border-color);
  color: var(--text-secondary);
  transition: all 0.2s ease;
  height: 100%;
  position: relative;
  gap: 0.5rem;

  &:hover {
    color: var(--text-primary);
    background: rgba(255, 255, 255, 0.02);
  }

  &.active {
    color: var(--text-primary);
    border-bottom: 2px solid var(--accent-orange);
    
    &::after {
      content: '';
      position: absolute;
      bottom: -1px; /* Align with border-bottom */
      left: 0;
      width: 100%;
      height: 2px;
      background: var(--accent-orange);
    }
  }
}

.label {
  color: inherit;
}

.extra {
  display: flex;
  height: 100%;
  border-left: 1px solid var(--border-color);
}

.icon {
  margin-left: 0.5rem;
}

@media (max-width: 1024px) {
  .logo {
    width: auto;
    border-right: none;
  }

  .mobileToggle {
    display: block;
  }
  
  .links {
    display: none;
    position: fixed;
    top: 56px;
    left: 0;
    width: 100%;
    height: calc(100vh - 56px);
    background: var(--bg-primary);
    flex-direction: column;
    z-index: 1000;
    border-top: 1px solid var(--border-color);

    &.mobileOpen {
      display: flex;
    }
  }

  .link {
    width: 100%;
    height: 60px;
    padding: 0 1.5rem;
    border-right: none;
    border-bottom: 1px solid var(--border-color);

    &.active {
      border-bottom: 1px solid var(--border-color);
      color: var(--accent-orange);
      
      &::after {
        display: none;
      }
    }
  }

  .extra {
    display: none;
  }
  
  .navbar {
    padding: 0;
  }
}
